html {
  scroll-behavior: smooth;
  height: 100%;
}

body {
  font-family: 'Roboto', sans-serif;
  font-size: 12px;
  line-height: 16px;
  font-weight: 400;
  min-height: 100%;
  display: grid;
  grid-template-rows: auto 1fr auto;
  overflow-y: scroll;
}

.empty {
  background-image: url(../images/empty.webp);
  background-size: cover;
  background-position: center;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}
ul {
  padding: 0;
  margin: 0;
  list-style: none;
}
a {
  text-decoration: none;
  color: inherit;
}
.container {
  background-color: inherit;
  width: 100%;
  padding-right: 20px;
  padding-left: 20px;
  margin-right: auto;
  margin-left: auto;

  @media screen and (min-width: 320px) {
    width: 320px;
  }

  @media screen and (min-width: 768px) {
    width: 768px;
    padding-right: 75px;
    padding-left: 75px;
  }

  @media screen and (min-width: 1024px) {
    width: 1024px;
    padding-right: 71px;
    padding-left: 71px;
  }
}
.light-theme {
  background-color: $main-bg-color;
  color: $text-color;

  & .prewiev-card:hover {
    box-shadow: 5px -5px 10px -2px $text-color;
  }
}
.dark-theme {
  background-color: $text-color;
  background-image: url(../images/desktop/bg-dark-4783.png);
  background-position: center center;
  color: $main-bg-color;

  & .prewiev-card:hover {
    box-shadow: 5px -5px 10px -2px $accent-color;
  }
}

.visuallyhidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  height: 1px;
  width: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
}
