.svg {
  width: 35px;
  height: 35px;
}
.content-modal {
  position: relative;
}

.btn-close {
  position: absolute;
  top: 10px;
  right: 10px;
  width: 35px;
  height: 35px;
  padding: 0;
  background-color: transparent;
  border: none;
  border-radius: 50%;
  outline: none;
  transition-property: box-shadow;
  transition-duration: 400ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  cursor: pointer;
}
.btn-close:hover,
.btn-close:focus {
  box-shadow: 0 2.8px 2.2px #9b8573, 0 6.7px 5.3px #da690c, 0 1.5px 10px rgba(221, 126, 37, 0.856),
    0 2.3px 17.9px rgba(221, 126, 37, 0.856);
}

.btn-close svg {
  // stroke: #000000;
  transition-property: stroke;
  transition-duration: 400ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.btn-close svg:hover,
.btn-close svg:focus {
  // stroke: #ff6b08;
  fill: #ff6b08;
}
